.dropdown__selected__lang {
  display: block;
  margin-right: -7px;
  margin-left: 3px;
  padding: 4px 9px 4px;
  font-size: 0;
  &:after {
    
    content: '';
    display: block;
  }
  &_en {
    &:after {
      @include sprite(lRu);
    }
  }
  &_ru {
    &:after {
      @include sprite(lRu);
    }
  }
  &_ua {
    &:after {
      @include sprite(lRu);
    }
  }
  &_fr {
    &:after {
      @include sprite(lRu);
    }
  }
}
.dropdown__lang_en {
  font-size: 24px;
  @include sprite(lRu);
}
.dropdown__lang {
  ._ico {
    display: block;
    font-size: 24px;
  }
}
.dropdown__lang__content {
  ._ico {
    display: none;
  }
}