@import '__input/search-form__input.scss';
@import '__nav/search-form__nav.scss';
@import '__close/search-form__close.scss';
.search-form {
  left: 0;
  overflow: hidden;
  position: absolute;
  top: 100%;
  width: 100%;
  &__content {
    z-index: 10;
    background-color: $white;
    left: 0;
    position: relative;
    right: 0;
    top: -69px;
    height: 69px;
    z-index: 12;
  }
}
.search-form__link__icon {
  margin-top: -15px;
  &:before {
    padding-right: 7px;
    vertical-align: bottom;
  }
}
